import { Component, ViewChild, ElementRef, Renderer2 } from '@angular/core';
import { from, of, scheduled, animationFrameScheduler, Observable } from 'rxjs';
import { concatMap, delay } from 'rxjs/operators';
import { AppService } from './app.service';

export const ROCK = 'Rock';
export const PAPER = 'Paper';
export const SCISSORS = 'Scissors';
export const LIZARD = 'Lizard';
export const SPOCK = 'Spock';


export const WON_MESSAGE = 'You WON';
export const LOST_MESSAGE = 'You Lost';
export const TIE_MESSAGE = 'TIE';

export const RESULT_MAP = {
  [ROCK]: {
    [PAPER]: -1,
    [ROCK]: 0,
    [SCISSORS]: 1,
    [LIZARD]: 1,
    [SPOCK]: -1
  },
  [PAPER]: {
    [PAPER]: 0,
    [ROCK]: 1,
    [SCISSORS]: -1,
    [LIZARD]: 1,
    [SPOCK]: 1
  },
  [SCISSORS]: {
    [PAPER]: 1,
    [ROCK]: -1,
    [SCISSORS]: 0,
    [LIZARD]: -1,
    [SPOCK]: 1
  },
  [LIZARD]: {
    [PAPER]: 1,
    [ROCK]: -1,
    [SCISSORS]: -1,
    [LIZARD]: 0,
    [SPOCK]: 1
  },
  [SPOCK]: {
    [PAPER]: -1,
    [ROCK]: 1,
    [SCISSORS]: 1,
    [LIZARD]: -1,
    [SPOCK]: 0
  }
};

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  @ViewChild('compRes') compRes: ElementRef;
  public userSelections = [
    {
      name: SCISSORS,
      class: 'fa-hand-scissors-o'
    },
    {
      name: PAPER,
      class: 'fa-hand-paper-o'
    },
    {
      name: ROCK,
      class: 'fa-hand-rock-o'
    },
    {
      name: LIZARD,
      class: 'fa-hand-lizard-o'
    },
    {
      name: SPOCK,
      class: 'fa-hand-spock-o'
    },
  ];

  public userResult;
  public matchResult;

  constructor( private appService: AppService) { }

  public select(userSelect): void {
    const compSelect = this.userSelections[this.appService.getComputerResult(this.userSelections.length)];
    this.userResult = userSelect;
    this.appService.changingIconOfComputerSelect(this.userSelections, this.compRes)
      .subscribe({
        complete: () => {
          this.appService.removeAllClasses(this.compRes);
          this.appService.addClass(this.compRes, compSelect.class);
          const result = RESULT_MAP[userSelect.name][compSelect.name];
          this.matchResult = result === -1 ? LOST_MESSAGE : result === 1 ? WON_MESSAGE : TIE_MESSAGE;
        }
      });
  }
}
