import { ElementRef, Injectable, Renderer2, RendererFactory2 } from '@angular/core';
import { animationFrameScheduler, from, Observable, of, scheduled } from 'rxjs';
import { concatMap, delay } from 'rxjs/operators';

@Injectable(
    {
        providedIn: 'root'
    }
)
export class AppService {
    private renderer: Renderer2;

    constructor(rendererFactory: RendererFactory2) {
        this.renderer = rendererFactory.createRenderer(null, null);
    }

    public getComputerResult(options: number): number {
        return Math.floor(Math.random() * options);
    }

    public changingIconOfComputerSelect(userSelections, compResultElement): Observable<unknown> {
        return from(userSelections)
            .pipe(
                concatMap((select: { name: string, class: string }) => {
                    this.removeAllClasses(compResultElement);
                    this.addClass(compResultElement, select.class);
                    return scheduled(of(select), animationFrameScheduler).pipe(
                        delay(250)
                    );
                }));
    }

    public removeAllClasses(el: ElementRef): void {
        const classes = el.nativeElement.getAttribute('class').split(' ');
        classes.forEach((cssClass) => {
            this.renderer.removeClass(el.nativeElement, cssClass);
        });
    }

    public addClass(el: ElementRef, className): void {
        this.renderer.addClass(el.nativeElement, 'fa');
        this.renderer.addClass(el.nativeElement, className);
    }

}
